.navigation {
    background-color: #fff;
    padding: 40px;
    font-size: 14px;
    
}

.navigation ul {
    font-family: Roboto;
    font-weight: 600;
    list-style-type: none;
}

.navigation a {
    text-decoration: none;
    text-transform: uppercase;
    line-height: 20px;
}
.logo {
    background-color: #fff;
    padding: 40px;
    text-align: center;
}

.logo hr {
    background: #c7ad88;
    width: 60px;
    height: 1px;
    border: 0px;
}

.logo h1 {
    font-family: Roboto;
    color: #555555;
    font-size: 80px;
    letter-spacing: 3px;
    margin: 40px 0;
}

.logo h2 {
    text-align: left;
    color: #555555;
    letter-spacing: 2px;
}

.logo h1 span,
.logo h2 span {
    color: #c7ad88;
}


.footer {
    background-color: #fff;
    padding: 40px;
}

.footer__media {
    display: flex;
    margin-bottom: 30px;
    justify-content: space-between;
}

.footer__media-single {
    padding: 5px;
    border: 2px solid #f0f0f0;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    color: #555555;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    transition: all 0.2s ease-in-out;
}

.footer__media-single:hover {
    background-color: #c7ad88;
    border: 2px solid #c7ad88;
    color: #fff;
}

.home {
    background-image: url(../../static/media/home-background.e3151eed.jpg);
    background-position: center;
    background-size: cover; 
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    position: relative;
    -webkit-animation-name: background;
            animation-name: background;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
}

.home h2 {
    color: #f0f0f0;
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 20px;
}

.home p {
    text-transform: uppercase;
    color: #f0f0f0;
}

.home .button {
    border: 1px solid #c7ad88;
    color: #c7ad88;
    font-family: 'Poppins';
    width: 200px;
    height: 40px;
    font-size: 20px;
    line-height: 40px;
    text-align: center;
    box-shadow: 0;
    transition: all 0.2s ease-in-out;
}

.home .button:hover {
    background-color: #c7ad88;
    border: 1px solid #c7ad88;
    color: #fff;
    width: 202px;
    height: 41px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.home .title {
    margin-top: 40px;
    text-align: center;
}

@-webkit-keyframes background {
    0%   {top:2000px;}
    100% {top:0px;}
}

@keyframes background {
    0%   {top:2000px;}
    100% {top:0px;}
}

.projects-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.single-project {
    flex: 1 1;
    min-width: 350px;
    min-height: 350px;
    margin: 5px;
    background: #c7ad88;
}

.single-project:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.contact-img {
    background-image: url(../../static/media/contact.f1a0d3e5.jpg);
    background-position: center;
    background-size: cover;
    height: 450px;
    padding: 70px;
    position: relative;
}

.contact-border {
    border: 3px solid #c7ad88;
    height: 310px;
}

.contact-container h2 {
    left: 100px;
    bottom: 40px;
    position: absolute;
    color: #fff;
    font-size: 48px;
}

.contact-details {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
}

.contact-details-item {
    margin: 20px;
    text-align: center;
    color: #666666;
    font-size: 16px;
    font-family: "Poppins", sans-serif;
    text-transform: uppercase;
    font-weight: 600; 
}

.contact-details-item svg {
    font-size: 20px;
    color: #222222;
    margin-right: 30px;
}


  
section {
    width: 100%;
    height: 100vh;
    padding: 40px;
}

.content {
    width: 100%;
    height: 100%;
}




* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    color: #333333;
    font-size: 48px;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    line-height: 60px;
    margin: 0 0 20px; 
}
  
h2 {
    color: #333333;
    font-size: 24px;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    margin: 0 0 40px; 
}

p {
    font-size: 14px;
    color: #999999;
    font-family: "Merriweather", serif;
    font-weight: 300;
    line-height: 26px;
    margin: 0 0 16px; 
}

a {
    display: inline-block;
    text-decoration: none !important;
    transition: all 0.2s ease-in-out;
}

header {
    width: 325px;
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


header nav ul li {
    display: block;
    position: relative;
}

header nav ul li a {
    padding: 10px 0;
    color: #666666;
    font-size: 14px;
    font-family: "Poppins", sans-serif;
    text-transform: uppercase;
    font-weight: 600; 
}

header nav ul li a:before {
    display: inline-block;
    content: '';
    width: 0;
    height: 1px;
    margin-right: 0;
    margin-top: 8px;
    transition: all 0.3s ease-in-out;
    background: #c7ad88; 
}

header nav ul li a:hover {
    color: #c7ad88; 
}

header nav ul li a:hover:before {
    width: 60px;
    margin-right: 20px; 
}

section {
    margin-left: 325px;
}

.app-container {
    display: flex;
    height: 100vh;
    position: relative;
}

.back-to-top {
    position: absolute;
    
    right: 50px;
    bottom: 50px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #c7ad88;
    border: none;
}

.back-to-top svg {
    color: #fff;
    font-size: 20px;
}

@media screen and (max-width: 575px) {
    
}

@media all and (max-width: 768px) { 
    .app-container {
        flex-direction: column;
    }

    section {
        margin-left: 0;
    }

    header {
        position: unset;
        width: 100%;
        text-align: center;
    }
 }
 
